.chat-app {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    font-family: 'Montserrat', sans-serif;
  }
  
  .chat-header {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
  }
  
  .chat-header h1 {
    font-size: 2rem;
    font-weight: bold;
    color: #0047ab;
  }
  
  .chat-messages {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 300px;
    overflow-y: auto;
  }
  
  .chat-message {
    align-self: flex-start;
    max-width: 70%;
    padding: 15px;
    background-color: #f4f4f4;
    border-radius: 10px;
    font-size: 1.2rem;
    line-height: 1.5;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  }
  
  .chat-message:last-child {
    margin-bottom: 20px;
  }
  
  .chat-message.sent {
    align-self: flex-end;
    background-color: #c9f5f6;
    color: #0047ab;
  }
  
  .chat-input {
    display: flex;
    gap: 10px;
    position: sticky;
    bottom: 0;
    margin-top: 20px;
  }
  
  .chat-input input {
    flex-grow: 1;
    padding: 15px;
    border: none;
    background-color: #f4f4f4;
    border-radius: 10px;
    font-size: 1.2rem;
    line-height: 1.5;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  }
  
  .chat-input button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
  }
  
  .chat-input button img {
    width: 30px;
    height: 30px;
  }